body {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: rgb(63, 62, 62);
}
p {
  display: inline;
  margin-left: 1rem;
  font-size: 1.5em;
}
.fa,
.fas {
  font-weight: 900;
  font-size: 1.5em;
  color: #00ff00;
}
#container {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  align-content: center;
  justify-content: center;
}

#app {
  display: flex;
  flex-direction: column;
  padding: 2em;
  background: #fafafaa2;
  height: 715px;
  width: 935px;
  background-color: rgba(95, 94, 94, 0.836);
  border: 1px solid black;
  box-shadow: 0 0 1em rgba(52, 253, 12, 0.993);
  border-radius: 30px;
}
.top-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 40%;
}
.bottom-container {
  margin-top: 40px;
  border-top: 2px solid black;
  border-bottom: 2px solid black;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 10px;
  overflow-y: scroll;
}
/* Hide scrollbar for Chrome, Safari and Opera */
.bottom-container::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.bottom-container {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

#header {
  width: 100%;
  height: 33%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
#header h1 {
  margin: 0;
}
#header p {
  margin: 0;
}
#input_field {
  width: 100%;
  height: 33%;
  justify-content: space-evenly;
  display: flex;
  align-items: center;
}
#input_field2 {
  width: 100%;
  height: 33%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
#input_field2 > p {
  display: flex;
  align-self: flex-end;
}

#category {
  width: 25%;
  height: 70%;
}

#task {
  width: 70%;
  height: 70%;
}
#priority {
  width: 30%;
  height: 70%;
}
