.saved-recipe-container {
  width: 100%;
  margin-top: 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
h1 {
  color: BLACK;
  text-align: center;
}
#recipe-container {
  height: 90%;
  width: 60%;
  /* margin-right:600px; */
  margin-top: 5%;
  /* background-color: rgba(255, 255, 255, 0.466); */
  display: flex;
  justify-content: space-around;
}

.form-group {
  display: flex;
  justify-content: space-between;
  margin-bottom: 4px;
  align-items: baseline;
}
input {
  font-size: 1.3rem;
}

option {
  background-color: #b3b1b1;
}
option :checked,
option :hover {
  background-color: darkblue;
}
.form-control {
  background-color: transparent;
  color: black;
  border-top: 0;
  border-right: 0;
  border-left: 0;
  border-radius: 0;
  font-size: 1.3rem;
}
::-webkit-input-placeholder {
  color: transparent;
}

.form-control:focus {
  border-color: #111;
}

.list-inline {
  display: flex;
  justify-content: center;
}
