/* bootstrap card */
.card_saved {
  position: inherit;
  display: flex;
  width: 315px;
  height: -moz-fit-content;
  height: 800px;
  padding: 1%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background-color: rgba(95, 94, 94, 0.959);
  border: 3px solid black;
  border-style: solid;
  border-width: 1px;
  box-shadow: 0 0 1em rgba(52, 253, 12, 0.993);
  margin-top: 4%;
}
.card-title {
  font-family: 'Indie Flower', cursive;
  font-size: 1.4em;
  color: #00ff00;
  /* margin-bottom: 3%; */
  height: 7%;
  text-align: center;
}
.card img {
  width: 85%;
  border-radius: 20px;
  border: 1px solid black;
}
div.card-body {
  width: 100%;
}
.ingredients-title {
  font-size: 1rem;
  width: 100%;
  text-align: center;
  font-size: 1vw;
  text-transform: uppercase;
  color: #00ff00;
}
.ingredients-container {
  display: flex;
  text-align: center;
  font-family: 'Indie Flower', cursive;
  font-size: 1em;
  color: #000000;
  margin-top: 1%;
  margin-bottom: 25px;
  display: flex;
  justify-content: center;
  max-height: 70px;
  width: 100%;
  overflow-y: auto;
}
ul {
  list-style-type: none;
}
.button-container {
  width: 100%;
  height: 27%;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.btn {
  /* margin-top: 40px; */
  /* font-size: 18px; */
  text-decoration: none;
  border: none;
  /* padding: 7px 40px; */
  background-color: rgb(57, 58, 57);
  color: #00ff00;
  border-radius: 5px;
  box-shadow: 7px 6px 28px 1px rgba(0, 0, 0, 0.24);
  cursor: pointer;
  outline: none;
  transition: 0.2s all;
}
.btn:hover,
.btn:focus {
  background-color: rgb(44, 44, 44);
  color: #00ff00;
}
.btn:active {
  transform: scale(0.98);
  /* Scaling button to 0.98 to its original size */
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
  /* Lowering the shadow */
}
.saved-recipe-form-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  padding: 2em;
  background: #fafafaa2;
  margin-top: 10%;
  height: 800px;
  width: 650px;
  background-color: rgba(95, 94, 94, 0.836);
  right: 5%;
  border-radius: 30px;
}
.text-form {
  display: flex;
  flex-direction: column;
}
textarea {
  background-color: rgba(32, 186, 1, 0.474);
}
select {
  background-color: rgba(32, 186, 1, 0.474);
}

@media screen and (max-width: 530px) {
  .card {
    margin-top: 80px;
    display: flex;
    width: 270px;
    height: 430px;
    border-width: 1px;
    box-shadow: 0 0 1em rgb(52 253 12 / 99%);
  }
  .card img {
    width: 55%;
    border-radius: 20px;
    border: 1px solid black;
  }
}
